.selected {
  position: relative;
  width: 200px;
  margin-bottom: 25px; }

.selected_label {
  position: relative;
  margin-left: 36px;
  cursor: pointer; }

.selected_label::before {
  content: "";
  position: absolute;
  left: -36px;
  width: 24px;
  height: 24px;
  border: 1px solid #c4c4c4;
  border-radius: 5px;
  cursor: pointer; }

.selected_label:hover::before {
  border: 1px solid #1a73e8; }

.selected:focus .selected_label::before {
  background-color: #eeeff2;
  border-radius: 5px;
  outline: none; }

.selected:focus {
  outline: none; }

.selected_input:checked + .selected_label::after {
  content: "";
  position: absolute;
  top: 6px;
  left: -31px;
  width: 16px;
  height: 14px;
  background-image: url("../img/check.png");
  background-repeat: no-repeat;
  cursor: pointer; }

