.page-header {
  background-image: url("../img/bg-menu.jpg");
  background-repeat: no-repeat;
}

.page-header_nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.site-menu_list {
  @include reset-list;

  display: flex;
  flex-direction: row;
}

.navbar-toggler-icon {
  background-image: url("../img/icon-menu.png");
  background-repeat: no-repeat;
  background-position: center;

  &:hover {
    opacity: 0.7;
  }
}

.navbar-collapse {
  display: flex;
  justify-content: flex-end;
  width: auto;
}

.site-menu_item {
  margin-right: 40px;

  &:last-child {
    margin-right: 0;
  }
}

.page-header_img {
  &:hover {
    opacity: 0.7;
  }
}

.site-menu_link {
  @include main-text;

  text-decoration: none;

  cursor: pointer;

  &:hover {
    color: $basic-blue;
  }
}

.user-nav {
  @include reset-list;

  display: flex;
  justify-content: space-between;
  margin-left: 100px;
}


.user-nav_link {
  @include medium-text;

  padding-left: 20px;

  text-decoration: none;

  cursor: pointer;

  &:hover {
    color: $basic-blue;
  }
}

.user-nav_link--sign-in {
  margin-right: 30px;

  background-image: url("../img/user.png");
  background-repeat: no-repeat;
  background-position: left center;
}

.user-nav_link--sign-up {
  padding-left: 30px;

  background-image: url("../img/user-in.png");
  background-repeat: no-repeat;
  background-position: left center;
}


@media screen and (max-width: 991px) {
  .page-header {
    flex-wrap: wrap;
  }

  .navbar-expand-lg {
    flex-wrap: wrap;
  }

  .navbar-collapse {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
    background-color: $special-light-grey;
    border-radius: 20px;
    box-shadow: 0 0 10px 10px rgba(0, 0, 0, 0.1);
  }

  .site-menu_item {
    margin-bottom: 20px;
  }

  .site-menu_list {
    margin-left: 80px;
    margin-top: 30px;
  }

  .user-nav {
    @include reset-list;
    margin-top: 30px;
    display: flex;
    justify-content: flex-start;
    width: 100%;
    padding-left: 40px;
    margin-bottom: 30px;
    margin-left: 0;
  }

  .user-nav_item {
    &:first-child {
      margin-right: 30px;
    }
  }

  .site-menu_list,
  .user-nav {
    width: 100%;
  }
}

@media screen and (max-width: 767px) {
  .site-menu_list {
    margin-left: 40px;
  }

  .user-nav {
    padding-left: 20px;
  }
}
